.nav {
  width: 100%;
  justify-content: space-between;
}

.nav,
.left,
.right {
  display: flex;
  gap: 40px;
}

.left {
  flex: 1;
}

.right {
  flex: 1;
  justify-content: flex-end;
}

.button {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav img {
  height: 20px;
  width: 20px;
}

.nav a {
  text-decoration: none;
  color: var(--font-color);
}

.button-name {
  font-weight: 900;
  color: var(--font-color);
}

.search {
  display: flex;
  align-items: center;
  flex: 1;
  gap: 5px;
}

.search input {
  flex: 1;
  height: 30px;
  border-radius: 10px;
  background-color: var(--light-black);
  border: none;
  outline: none;
  font-family: inherit;
  font-size: 1rem;
  padding: 0px 15px;
  color: var(--font-color);
}

.search img {
  cursor: pointer;
}

.search input:active {
  transform: scale(0.99);
  transition: transform 0.3s;
}

.button {
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .home-button,
  .github-button {
    display: none;
  }
}

@media screen and (max-width: 460px) {
  .search > img {
    display: none;
  }

  .search,
  .search input {
    width: 25px;
  }
}
